<template>
  <v-app>
    <v-app-bar app dark height="70">
      <div class="d-flex align-center">
        <v-img
          alt="Vuetify Name"
          class="shrink mt-1 hidden-sm-and-down me-3"
          contain
          min-width="250"
          src="https://www.bluecore.com.br/img/logo_bluecoreDP.svg"
          width="250"
          style="filter: brightness(0) invert(1);"
        />
        <!-- <v-menu ref="menu" v-model="menu" :close-on-content-click="false">
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="date"
              prepend-icon="mdi-calendar"
              label="Insira o periodo de busca"
              readonly
              outlined
              v-bind="attrs"
              v-on="on"
              class="ps-3 pt-8"
            ></v-text-field>
          </template>
          <v-date-picker v-model="date" color="light-blue lighten-4" no-title scrollable range></v-date-picker>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
          <v-btn text color="primary" @click="$refs.menu.save(date)">OK</v-btn>
        </v-menu> -->
      </div>
      <v-progress-linear
        :active="loading"
        :indeterminate="loading"
        fixed
        top
        height="10"
        color="blue"
      ></v-progress-linear>
    </v-app-bar>

    <v-main>
      <Home />
    </v-main>
  </v-app>
</template>

<script>
import Home from './components/Home';
import Api from './helpers/api';

export default {
  name: 'App',

  components: {
    Home
  },

  data: () => ({
    date: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    menu: false,
    loading: false
  }),
  mounted() {

    var api = new Api()

    // before a request is made start the request
    api.axios.interceptors.request.use(config => {
      this.loading = true;
      return config
    });

    // before a response is returned end request
    api.axios.interceptors.response.use(response => {
      this.loading = false;
      return response
    });
  }
};
</script>
