<template>
    <v-container>
        <v-flex v-if="show == 'boards'">
            <v-row class="mb-2">
                <v-col>
                    <h2>
                        Tach
                        <v-icon>mdi-graphql</v-icon>GraphQL - Boards
                    </h2>
                </v-col>
            </v-row>
            <v-text-field
                v-model="boardsSearch"
                append-icon="mdi-magnify"
                label="Search"
                single-line
                hide-details
            ></v-text-field>
            <v-data-table
                :headers="boardsHeaders"
                :items="boards"
                :search="boardsSearch"
                multi-sort
                class="elevation-1"
                show-select
                single-select
                v-on:item-selected="boardSelected"
                dark
            ></v-data-table>
        </v-flex>
        <v-flex v-if="show == 'items'">
            <v-row class="mb-2">
                <v-col>
                    <h2>
                        Tach
                        <v-icon>mdi-graphql</v-icon>GraphQL - Board Selecionado
                        <v-icon>mdi-greater-than</v-icon>
                        {{ selectedBoard.name }}
                    </h2>
                </v-col>
                <v-col class="text-right">
                    <v-btn
                        raised
                        color="grey darken-3"
                        v-on:click="backToBoards()"
                        class="me-1 mt-1"
                    >Voltar</v-btn>
                    <v-btn
                        raised
                        color="blue darken-4"
                        v-on:click="getTime"
                        class="me-1 mt-1"
                        v-if="items.length > 0"
                    >
                        <v-icon>mdi-av-timer</v-icon>Horas
                    </v-btn>
                </v-col>
            </v-row>
            <v-text-field
                v-model="itemsSearch"
                append-icon="mdi-magnify"
                label="Search"
                single-line
                hide-details
            ></v-text-field>
            <v-data-table
                :headers="itemsHeaders"
                :items="items"
                :search="itemsSearch"
                class="elevation-1"
                v-on:item-selected="itemSelected"
            ></v-data-table>
        </v-flex>
        <v-flex v-if="show == 'horas'">
            <v-row class="mb-2">
                <v-col>
                    <h2>
                        Tach
                        <v-icon>mdi-graphql</v-icon>GraphQL - Board Selecionado
                        <v-icon>mdi-greater-than</v-icon>
                        {{ selectedBoard.name }}
                        <v-icon>mdi-greater-than</v-icon>Horas
                    </h2>
                </v-col>
                <v-col cols="12" sm="4" md="2">
                    <v-menu
                        ref="menu"
                        v-model="openDatePicker"
                        :close-on-content-click="false"
                        transition="scale-transition"
                        offset-y
                    >
                        <template v-slot:activator="{ on, attrs }">
                            <v-text-field
                                v-model="rangeDates"
                                prepend-icon="mdi-calendar"
                                label="Insira o periodo de busca"
                                readonly
                                outlined
                                v-bind="attrs"
                                v-on="on"
                            ></v-text-field>
                        </template>
                        <v-date-picker
                            v-model="rangeDates"
                            color="blue darken-4"
                            no-title
                            scrollable
                            range
                        ></v-date-picker>

                        <v-btn
                            ccolor="grey darken-3"
                            @click="openDatePicker = false"
                            class="me-1 mt-1"
                        >Cancel</v-btn>
                        <v-btn
                            ccolor="grey darken-3"
                            @click="rangeDates = []"
                            class="me-1 mt-1"
                        >Clear</v-btn>
                        <v-btn
                            color="blue darken-4"
                            @click="filterTimeTracking()"
                            class="me-1 mt-1"
                        >OK</v-btn>
                    </v-menu>
                </v-col>
                <v-col class="text-right">
                    <v-btn
                        raised
                        color="grey darken-3"
                        v-on:click="backToItems()"
                        class="me-1 mt-1"
                    >Voltar</v-btn>
                    <v-btn
                        raised
                        color="blue darken-4"
                        v-on:click="createExcel()"
                        class="me-1 mt-1"
                    >
                        <v-icon>mdi-microsoft-excel</v-icon>Excel
                    </v-btn>
                </v-col>
            </v-row>
            <v-simple-table dense>
                <template v-slot:default>
                    <thead>
                        <tr>
                            <th v-for="header in timeHeaders" :key="header.value">{{ header.text }}</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="time in time_tracking_filtered" :key="time.value">
                            <td
                                v-for="header in timeHeaders"
                                :key="header.key"
                            >{{ getValue(time, header) }}</td>
                        </tr>
                    </tbody>
                </template>
            </v-simple-table>
        </v-flex>
    </v-container>
</template>

<!-- eslint-disable -->

<script>

import moment from 'moment';
import Boards from '../helpers/boards';
import Items from '../helpers/items';
import Users from '../helpers/users';
import TimeValue from '../models/time_value_excel'

export default {
    name: "Home",
    data() {
        return {
            rangeDates: [],
            openDatePicker: false,
            boardsHeaders: [],
            boards: [],
            boardsSearch: "",
            selectedBoards: [],
            selectedBoard: {},

            items: [],
            itemsHeaders: [],
            itemsSearch: "",
            selectedItems: [],
            selectedItem: {},

            show: 'boards',

            timeHeaders: [],
            timeSearch: "",
            time_registers: [],
            time_tracking: [],
            time_tracking_filtered: [],
            users: [],
        }
    },
    mounted() {
        this.getBoards();
        this.getUsers();
    },
    methods: {
        getValue(item, header) {

            return item[header.value];
        },
        //boards
        getBoards() {
            const boards = new Boards();
            boards.get().then(result => {
                this.boards = result.data.data.boards;
                this.boardsHeaders = [];
                for (const key in this.boards[0]) {
                    this.boardsHeaders.push({ text: key, value: key });
                }
            });
        },
        boardSelected(args) {
            if (args.value) {
                this.show = 'items';
                this.selectedBoard = args.item;
                this.getItems(this.selectedBoard.id);
            }
            else
                this.selectedBoard = null;
        },
        backToBoards() {
            this.show = 'boards'
            this.items = [];
        },

        //items
        getItems(boardId) {
            const items = new Items();
            items.get(boardId).then(result => {
                this.items = result.data.data.boards[0].items;

                if (this.items.length > 0) {
                    this.itemsHeaders = [];
                    for (const key in this.items[0]) {
                        if (key != 'column_values') {
                            this.itemsHeaders.push({ text: key, value: key });
                        }
                    }

                    for (const item of this.items) {
                        for (const column_value of item.column_values) {

                            if (column_value.id == 'controle_de_tempo'
                                && column_value.value != null) {
                                var register = JSON.parse(column_value.value);
                                register.item_name = item.name;

                                this.time_registers.push(register);
                            }

                        }
                    }
                }

            });
        },
        itemSelected(args) {
            if (args.value) {
                this.selectedItem = args.item;
            }
            else
                this.selectedBoard = null;
        },
        backToItems() {
            this.show = 'items';
            this.time_tracking = [];
        },
        getTime() {

            this.show = 'horas';
            var times = [];

            for (var index = 0; index < this.time_registers.length; index++) {
                const register = this.time_registers[index];

                if (register.additional_value) {

                    for (var jindex = 0; jindex < register.additional_value.length; jindex++) {
                        const time_value = register.additional_value[jindex]
                        time_value.item_name = register.item_name;

                        var started_user = this.findUser(time_value.started_user_id);
                        if(started_user === undefined || started_user == null){
                            time_value.started_user_name = time_value.started_user_id;
                        }
                        else
                            time_value.started_user_name = started_user.name;

                        var ended_user = this.findUser(time_value.ended_user_id);
                        if (ended_user === 'undefined' || ended_user == null)
                            time_value.ended_user_name = time_value.ended_user_id;
                        else
                            time_value.ended_user_name = ended_user.name;

                        times.push(time_value);
                    }
                }
            }

            this.time_tracking = times;

            this.timeHeaders = [
                { text: 'Id', value: 'id' },
                { text: 'Item', value: 'item_name' },
                { text: 'Started By ID', value: 'started_user_id' },
                { text: 'Started By', value: 'started_user_name' },
                { text: 'Start Date', value: 'started_at' },
                { text: 'Ended By', value: 'ended_user_name' },
                { text: 'Ended By ID', value: 'ended_user_id' },
                { text: 'Ended Date', value: 'ended_at' },
                { text: 'Status', value: 'status' }];

            this.filterTimeTracking();
        },
        createExcel() {

            var excelHeader = [
                { text: 'Id', value: 'id' },
                { text: 'Item', value: 'item_name' },
                { text: 'Started ID By', value: 'started_user_id' },
                { text: 'Started By', value: 'started_user_name' },
                { text: 'Start Date', value: 'started_at_date' },
                { text: 'Start Time', value: 'started_at_time' },

                { text: 'Ended By', value: 'ended_user_name' },
                { text: 'Ended Date', value: 'ended_at_date' },
                { text: 'Ended Time', value: 'ended_at_time' },
                { text: 'Duration', value: 'duration' }];

            var excelData = [];
            for (const time_value of this.time_tracking_filtered) {
                excelData.push(new TimeValue(time_value));
            }

            var excelMatrix = [[]];
            excelMatrix.push(excelHeader.map(header => header.text))

            for (const data of excelData) {
                var row = [];
                for (const header of excelHeader) {
                    row.push(data[header.value]);
                }
                excelMatrix.push(row);
            }

            var csvContent = "";
            excelMatrix.forEach((row) => {
                var cvsRow = "";
                row.forEach((cell) => {
                    cvsRow += `"${cell}";`;
                });
                csvContent += `${cvsRow}\n`;
            });
            
            console.log(csvContent);

            this.download(csvContent, `${new Date().getTime()}.csv`, "text/csv;encoding:utf-8,%EF%BB%BF")
        },
        download(content, fileName, mimeType) {
            var a = document.createElement("a");
            mimeType = mimeType || "application/octet-stream";

            if (navigator.msSaveBlob) {
                // IE10
                navigator.msSaveBlob(
                    new Blob([content], { type: mimeType }),
                    fileName
                );
            } else if (URL && "download" in a) {
                //html5 A[download]
                a.href = URL.createObjectURL(
                    new Blob([content], {
                        type: mimeType,
                    })
                );
                a.setAttribute(
                    "href",
                    "data:text/csv;charset=utf-8,%EF%BB%BF" + encodeURI(content)
                );
                a.setAttribute("download", fileName);
                document.body.appendChild(a);
                a.click();
                document.body.removeChild(a);
            } else {
                document.location.href =
                    "data:application/octet-stream," + encodeURIComponent(content); // only this mime type is supported
            }
        },
        filterTimeTracking() {

            this.openDatePicker = false;

            if (this.rangeDates.length == 1) {
                const minDate = this.getDateTime(this.rangeDates[0]);
                window.console.log(minDate);
                this.time_tracking_filtered = this.time_tracking.filter(time => { return this.getDateTime(time.started_at) >= minDate; });
            }
            else if (this.rangeDates.length == 2) {

                this.rangeDates = this.rangeDates.sort();

                const minDate = this.getDateTime(this.rangeDates[0]);
                const maxDate = this.getDateTime(this.rangeDates[1]);


                this.time_tracking_filtered = this.time_tracking.filter(time => {
                    return this.getDateTime(time.started_at) >= minDate && this.getDateTime(time.started_at) <= maxDate;
                });
            }
            else
                this.time_tracking_filtered = this.time_tracking;
        },
        getDateTime(date) {
            return new Date(moment.utc(date).toJSON().replace('Z', ''));
        },


        //users
        getUsers() {
            const users = new Users();
            users.get().then(result => {
                this.users = result.data.data.users;
            });
        },
        findUser(userId) {
            var result = this.users.find(user => user.id == userId);
            if (result)
                return JSON.parse(JSON.stringify(result))

            return null;
        },
    }
}

</script>
