import moment from "moment"

export default class TimeValue {
    constructor(time_value) {
        this.id = time_value.id;

        this.started_user_id = time_value.started_user_id;
        this.started_user_name = time_value.started_user_name;
        this.started_at = time_value.started_at;

        this.ended_user_id = time_value.ended_user_id;
        this.ended_user_name = time_value.ended_user_name;
        this.ended_at = time_value.ended_at;

        //o `Z` no final da data quer dizer que a data estaria em UTC (http://en.wikipedia.org/wiki/ISO_8601), 
        // um erro comum, na verdade algo esquecido... o fato eh que as
        // datas sempre aparecem de acordo com o que se vê no front-end da monday e não de acordo com o UTC
        // então eu removo o z para o browser não efetuar conversões de fuso horário
        this.started_at_date_obj = new Date(time_value.started_at.replace('Z', ''));
        this.started_at_date = this.started_at_date_obj.toLocaleDateString();
        this.started_at_time = this.started_at_date_obj.toLocaleTimeString();

        if (time_value.ended_at !== undefined && time_value.ended_at != null && time_value.ended_at.length > 0) {
            this.ended_at_date_obj = new Date(time_value.ended_at.replace('Z', ''));
            this.ended_at_date = this.ended_at_date_obj.toLocaleDateString();
            this.ended_at_time = this.ended_at_date_obj.toLocaleTimeString();
        }
        else {
            this.ended_at_date_obj = null;
            this.ended_at_date = '';
            this.ended_at_time = '';
        }

        if(this.ended_at_date_obj != null)        
            this.duration = moment.utc(moment(this.ended_at_date_obj).diff(this.started_at_date_obj)).format("HH:mm:ss");
        else
            this.duration = moment.utc(moment().diff(this.started_at_date_obj)).format("HH:mm:ss");

        this.item_name = time_value.item_name;
        this.status = time_value.status;
    }

}