import axios from 'axios';

var instance = null;

var api = () => {

    if (instance != null) {
        return instance;
    }

    instance = axios.create({
        baseURL: 'https://api.monday.com/v2',
        headers: {
            'Authorization': 'eyJhbGciOiJIUzI1NiJ9.eyJ0aWQiOjEyNTM2NTIzOCwidWlkIjoyMzcyOTI3NCwiaWFkIjoiMjAyMS0wOS0yMFQxODo0NjoyOC4wMDBaIiwicGVyIjoibWU6d3JpdGUiLCJhY3RpZCI6NDcxOTcxOSwicmduIjoidXNlMSJ9.i31witkEKntS4mug-0PUvOBGMOAJ1Qcr5xQ6wYSxOI0',
            'Content-Type': 'application/json'
        }
    });

    return instance;
}

export default class Api {

    get axios() { return api(); }
    set axios(value) { instance = value; }

    query(gqlString) {
        return api().post('/', JSON.stringify({ 'query': gqlString }));
    }
}