import Api from './api';

export default class Items extends Api {   
    get(boardId) {

        return this.query(
            'query { ' +
                'boards (ids:[' + boardId + ']) { ' +                    
                    'items { ' +
                        'id ' +
                        'name ' +                        
                        'state ' +
                        'column_values { ' +
                            'id ' +
                            'text ' +
                            'title ' +
                            'type ' +
                            'value ' +
                        '} ' +
                    '} ' +
                '} ' +
            '} '
        );
    }

    getAll(){
        const gql = `
            query {
                items () {
                    id 
                    name
                    board {
                        id 
                        name
                    }
                }
            }`;

        return this.query(gql);
    }
}