import Api from './api';

export default class Boards extends Api {   

    get() {
        return this.query(
            'query { ' +
                'boards (limit: 100) { ' +
                    'id ' +
                    'name ' +
                    'state ' + 
                    'description ' +
                    'pos ' + 
                    'updated_at ' +                     
                '} ' +
            '} ');
    }
}